:root {
    --clr-one: rgb(238, 238, 238);
    --clr-two: rgb(255, 247, 0);
    --clr-three: rgb(0, 254, 140);
    --clr-bg: #000000;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    min-height: 100vh;
    background: var(--clr-bg);
    font-family: "Balsamiq Sans", cursive;
}

.navbar-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    padding: 4rem;
    background: var(--clr-bg);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
}

.logo-div {
    flex: 1;
}

.logo {
    height: 50px;
}

.navbar-buttons-div {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    flex: 3;
    margin-left: auto;
    margin-right: auto;
}

.neon-button {
    font-size: 1rem;
    display: inline-block;
    cursor: pointer;
    text-decoration: none;
    padding: 0.5em 1em;
    margin: 0 1rem;
    border-radius: 0.25em;
    position: relative;
}

.neon-button__1 {
    color: var(--clr-one);
    border: var(--clr-one) 0.125em solid;
    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em var(--clr-one);
    box-shadow: inset 0 0 0.5em 0 var(--clr-one), 0 0 0.5em 0 var(--clr-one);
}

.neon-button__2 {
    color: var(--clr-two);
    border: var(--clr-two) 0.125em solid;
    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em var(--clr-two);
    box-shadow: inset 0 0 0.5em 0 var(--clr-two), 0 0 0.5em 0 var(--clr-two);
}

.neon-button__3 {
    color: var(--clr-three);
    border: var(--clr-three) 0.125em solid;
    text-shadow: 0 0 0.125em hsl(0 0% 100% / 0.3), 0 0 0.45em var(--clr-three);
    box-shadow: inset 0 0 0.5em 0 var(--clr-three), 0 0 0.5em 0 var(--clr-three);
}

.neon-button::before {
    content: "";
    position: absolute;
    top: 120%;
    left: 0;
    width: 100%;
    height: 100%;
    transform: perspective(1.5em) rotateX(45deg) scale(0.8, 0.3);
    filter: blur(1.5em);
    opacity: 0.7;
    pointer-events: none;
}

.neon-button.active::before {
    animation: scalingShadow 0.5s ease-in-out;
}

@keyframes scalingShadow {
    0% {
        top: 120%;
        transform: perspective(1.5em) rotateX(45deg) scale(0.8, 0.3);
    }
    50% {
        top: 165%;
        transform: perspective(1.5em) rotateX(45deg) scale(0.75, 0.3);
    }
    100% {
        top: 120%;
        transform: perspective(1.5em) rotateX(45deg) scale(0.8, 0.3);
    }
}

.neon-button__1::before {
    background: var(--clr-one);
}

.neon-button__2::before {
    background: var(--clr-two);
}

.neon-button__3::before {
    background: var(--clr-three);
}

.neon-button::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    opacity: 0;
    z-index: -1;
    transition: opacity 100ms linear;
}

.neon-button__1::after {
    box-shadow: 0 0 2em 0.5em var(--clr-one);
    background-color: var(--clr-one);
}

.neon-button__2::after {
    box-shadow: 0 0 2em 0.5em var(--clr-two);
    background-color: var(--clr-two);
}

.neon-button__3::after {
    box-shadow: 0 0 2em 0.5em var(--clr-three);
    background-color: var(--clr-three);
}

.neon-button:hover::before,
.neon-button:focus::before {
    opacity: 1;
}

.neon-button:hover::after,
.neon-button:focus::after {
    opacity: 1;
}

.neon-button:hover,
.neon-button:focus {
    outline: none;
    color: var(--clr-bg);
    text-shadow: none;
}

.active {
    animation: animate 0.5s ease-in-out;
}

@keyframes animate {
    0% {
        transform: translateY(0%);
    }
    50% {
        transform: translateY(-40%);
    }
    100% {
        transform: translateY(0%);
    }
}

/* Responsive styles */
@media (max-width: 1180px) {
    .navbar-container {
        padding: 3rem;
    }

    .navbar-buttons-div {
        padding: 1.5rem;
        justify-content: space-evenly;
    }

    .neon-button {
        font-size: 1rem;
        padding: 0.4em 0.8em;
        margin: 0 0.8rem;
    }
}

@media (max-width: 992px) {
    .navbar-container {
        padding: 2rem;
    }

    .navbar-buttons-div {
        padding: 1rem;
        justify-content: space-evenly;
    }

    .neon-button {
        font-size: 1.1rem;
        padding: 0.5em 1em;
        margin: 0 0.8rem;
    }
}

@media (max-width: 768px) {
    .navbar-container {
        padding: 1rem;
    }

    .navbar-buttons-div {
        padding: 1rem;
        justify-content: space-around;
    }

    .neon-button {
        font-size: 0.8rem;
        padding: 0.4em 0.8em;
        margin: 0 0.5rem;
    }
}

@media (max-width: 576px) {
    .navbar-container {
        flex-direction: column;
        align-items: flex-start;
        padding: 0.5rem;
    }

    .navbar-buttons-div {
        width: 100%;
        padding: 1rem;
        justify-content: space-evenly;
    }

    .neon-button {
        font-size: 1.5rem;
        padding: 0.5em 1em;
        margin: 0.3rem 0;
    }
}

@media (max-width: 480px) {
    .navbar-buttons-div {
        padding: 0.5rem;
    }

    .neon-button {
        font-size: 1.3rem;
        padding: 0.4em 0.8em;
        margin: 0.2rem 0;
    }
}

@media (max-width: 360px) {
    .navbar-buttons-div {
        padding: 0.5rem;
    }

    .neon-button {
        font-size: 1.2rem;
        padding: 0.3em 0.6em;
        margin: 0.2rem 0;
    }
}
