body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f4f4f4;
}

.homepage-container {
  text-align: center;
  color: #333;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  margin-top: 10vw;
}

.hero-section {
  color: white;
  padding: 5rem 2rem;
  flex: 1;
}

.hero-section h1 {
  font-size: 3rem;
  margin: 0 0 1rem;
}

.hero-section p {
  font-size: 1.25rem;
  margin-bottom: 2rem;
}

.cta-button {
  background-color: #ff7f50;
  color: white;
  padding: 0.75rem 1.5rem;
  border: none;
  border-radius: 5px;
  font-size: 1.4vw;
  cursor: pointer;
  text-decoration: none;
  transition: 0.5s ease-in-out;
}

.cta-button:hover {
  color: #ff6347;
  background-color: white;
}

.intro-section {
  background-color: rgb(0, 0, 0);
  padding: 4rem 2rem;
  flex: 1;
}

.intro-section h2 {
  font-size: 3vw;
  margin-bottom: 1rem;
  color: wheat;
}

.introPara {
  color: wheat;
  font-size: 1.5vw;
}

.service-highlights {
  display: flex;
  flex-direction: column; /* Default to column for smaller screens */
  justify-content: space-evenly;
  align-items: center;
}

.highlight {
  background-color: #e0e0e0;
  margin: 2rem 0;
  padding: 1.5rem;
  border-radius: 5px;
  width: 90%;
  max-width: 25vw;
  transition: 0.5s ease-in-out;
  text-align: left;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 200px; /* Set a minimum height */
}

.highlight:hover {
  transform: scale(1.05);
  box-shadow: 0 0 15px rgb(255, 255, 255);
}

.highlight h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
}

.highlight p {
  font-size: 1rem;
  flex-grow: 1; /* Allow the paragraph to take up remaining space */
}

.footer {
  background-color: #333;
  color: white;
  padding: 1rem;
  text-align: center;
  flex-shrink: 0;
}

/* For small screens */
@media (max-width: 767px) {
  .homepage-container {
    margin-top: 100vh;
  }

  .hero-section h1 {
    font-size: 2.5rem;
  }

  .hero-section p {
    font-size: 1rem;
  }

  .cta-button {
    font-size: 2.5vw;
  }

  .intro-section h2 {
    font-size: 5vw;
  }

  .introPara {
    font-size: 2.5vw;
  }

  .highlight {
    max-width: 90%;
  }

  .highlight p {
    font-size: 3vw;
  }
}

/* For extra-small screens */
@media (max-width: 480px) {
  .hero-section h1 {
    font-size: 2rem;
  }

  .hero-section p {
    font-size: 0.9rem;
  }

  .cta-button {
    font-size: 3vw;
  }

  .intro-section h2 {
    font-size: 6vw;
  }

  .introPara {
    font-size: 3vw;
  }

  .highlight {
    max-width: 100%;
  }

  .highlight p {
    font-size: 3.5vw;
  }
}

/* For 360 x 800 screens */
@media (max-width: 360px) {
  .homepage-container {
    margin-top: 10vh;
  }

  .hero-section h1 {
    font-size: 1.8rem;
  }

  .hero-section p {
    font-size: 0.8rem;
  }

  .cta-button {
    font-size: 3.5vw;
  }

  .intro-section h2 {
    font-size: 6.5vw;
  }

  .introPara {
    font-size: 3.2vw;
  }

  .highlight {
    max-width: 100%;
    padding: 1rem;
    margin: 1rem 0;
  }

  .highlight p {
    font-size: 4vw;
  }
}

/* For 820 x 1180 screens */
