  }

  .intro-section h2 {
    font-size: 3vw;
  }

  .introPara {
    font-size: 1.5vw;
  }

  .highlight p {
    font-size: 1.5vw;
  }
}

/* For 768 x 1024 screens */
@media (min-width: 768px) and (max-width: 1024px) {
  .homepage-container {
    margin-top: 9vh;
  }

  .hero-section h1 {
    font-size: 4.5vh;
  }

  .hero-section p {
    font-size: 2.5vh;
  }

  .cta-button {
    font-size: 2.5vh;
  }

  .intro-section h2 {
    font-size: 4.5vh;
  }

  .introPara {
    font-size: 2.5vh;
  }

  .highlight p {
    font-size: 1.5vw;
  }
}

/* For 320 x 800 screens */
@media (max-width: 320px) {
  .hero-section h1 {
    font-size: 1.8rem;
  }

  .hero-section p {
    font-size: 0.8rem;
  }

  .cta-button {
    font-size: 3.5vw;
  }

  .intro-section h2 {
    font-size: 6.5vw;
  }

  .introPara {
    font-size: 3.2vw;
  }

  .highlight {
    max-width: 100%;
    padding: 1rem;
    margin: 1rem 0;
  }

  .highlight p {
    font-size: 4vw;
  }
}

/* For large screens */
@media (min-width: 1024px) {
  .service-highlights {
    flex-direction: row; /* Change to row for larger screens */
  }

  .hero-section h1 {
    font-size: 4rem;
  }

  .hero-section p {
    font-size: 1.5rem;
  }

  .cta-button {
    font-size: 1.2vw;
  }

  .intro-section h2 {
    font-size: 3.5vw;
  }

  .introPara {
    font-size: 1.5vw;
  }

  .highlight p {
    font-size: 1.5vw;
  }
}

/* For extra-large screens */
@media (min-width: 1440px) {
  .service-highlights {
    flex-direction: row; /* Change to row for extra-large screens */
  }

  .hero-section h1 {
    font-size: 5rem;
  }

  .hero-section p {
    font-size: 2rem;
  }

  .cta-button {
    font-size: 1vw;
  }

  .intro-section h2 {
    font-size: 4vw;
  }

  .introPara {
    font-size: 1.25vw;
  }

  .highlight p {
    font-size: 1.25vw;
  }
}

/* For 884 x 1104 screens */
@media (min-width: 884px) and (max-width: 1104px) {
  .homepage-container {
    margin-top: 12vh;
  }

  .hero-section h1 {
    font-size: 2.8rem;
  }

  .hero-section p {
    font-size: 2.2vh;
  }

  .cta-button {
    font-size: 2.2vw;
  }

  .intro-section h2 {
    font-size: 4.5vh;
  }

  .introPara {
    font-size: 2.2vh;
    margin: 5vh;
  }

  .highlight {
    max-width: 80%;
    margin: 1vh;
  }

  .highlight p {
    font-size: 1.2rem;
  }
}
