.services-container {
  padding-top: 15vw;
}

/* Existing styles */

.services-hero-section {
  text-align: center;
  padding: 2rem 0;
  background-color: #f8f8f8;
}

.services-hero-section h1 {
  font-size: 2.5rem;
  margin-bottom: 1rem;
}

.services-hero-section p {
  font-size: 1.25rem;
  color: #555;
}

.services-details {
  display: flex;
  margin-left: 5vw;
  flex-direction: column;
  width: 90vw;
  gap: 2rem;
}

.service-card {
  display: flex;
  margin: 3vw;
  flex-direction: column;
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 8px;
  overflow: hidden;
  transition: 0.3s ease-in-out;
}

.service-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 0 5vw wheat;
}

.ServicesImageContainer {
  text-align: center;
  padding: 1rem;
}

.servicesImages {
  max-width: 100%;
  height: auto;
  border-bottom: 1px solid #ddd;
}

.service-card div:last-child {
  padding: 1rem;
}

.service-card h3 {
  font-size: 1.5rem;
  margin-bottom: 0.5rem;
  color: black;
}

.service-card p {
  font-size: 1rem;
  color: #666;
}

/* Responsive styles */

/* For small screens */
@media (max-width: 767px) {
  .services-container {
    padding-top: 25vw;
  }

  .services-hero-section h1 {
    font-size: 2rem;
  }

  .services-hero-section p {
    font-size: 1rem;
  }

  .service-card h3 {
    font-size: 1.25rem;
  }

  .service-card p {
    font-size: 0.875rem;
  }
}

/* For extra-small screens */
@media (max-width: 480px) {
  .services-container {
    padding-top: 30vw;
  }

  .services-hero-section h1 {
    font-size: 1.8rem;
  }

  .services-hero-section p {
    font-size: 0.9rem;
  }

  .service-card h3 {
    font-size: 1.15rem;
  }

  .service-card p {
    font-size: 0.8rem;
  }
}

/* For 360 x 800 screens */
@media (max-width: 360px) {
  .services-container {
    padding-top: 35vw;
  }

  .services-hero-section h1 {
    font-size: 1.6rem;
  }

  .services-hero-section p {
    font-size: 0.8rem;
  }

  .service-card h3 {
    font-size: 1rem;
  }

  .service-card p {
    font-size: 0.75rem;
  }
}

/* For 820 x 1180 screens */
@media (min-width: 820px) and (max-width: 1180px) {
  .services-container {
    padding-top: 10vw;
  }

  .services-hero-section h1 {
    font-size: 2.8rem;
  }

  .services-hero-section p {
    font-size: 1.2rem;
  }

  .service-card h3 {
    font-size: 1.75rem;
  }

  .service-card p {
    font-size: 1rem;
  }
}

/* For 768 x 1024 screens */
@media (min-width: 768px) and (max-width: 1024px) {
  .services-container {
    padding-top: 20vw;
    width: 80vw; /* Adjust width to 80vw */
  }

  .services-hero-section h1 {
    font-size: 2.5rem;
  }

  .services-hero-section p {
    font-size: 1.1rem;
  }

  .service-card h3 {
    font-size: 1.5rem;
  }

  .service-card p {
    font-size: 0.95rem;
  }
}

/* For 320 x 800 screens */
@media (max-width: 320px) {
  .services-container {
    padding-top: 40vw;
  }

  .services-hero-section h1 {
    font-size: 1.4rem;
  }

  .services-hero-section p {
    font-size: 0.7rem;
  }

  .service-card h3 {
    font-size: 0.95rem;
  }

  .service-card p {
    font-size: 0.7rem;
  }
}

/* For large screens */
@media (min-width: 1024px) {

  .services-details {
    flex-direction: row;
    flex-wrap: wrap;
  }

  .service-card {
    margin-top: 17vh;
    flex-direction: row;
    width: 80vw;
  }

  .ServicesImageContainer {
    flex: 1;
    padding: 1rem;
  }

  .service-card div:last-child {
    flex: 2;
    padding: 1.5rem;
  }
}

/* For extra-large screens */
@media (min-width: 1440px) {
  .services-container {
    padding-top: 10vw;
  }

  .services-hero-section h1 {
    font-size: 3rem;
  }

  .services-hero-section p {
    font-size: 1.5rem;
  }

  .service-card h3 {
    font-size: 1.75rem;
  }

  .service-card p {
    font-size: 1.25rem;
  }
}

/* For 884 x 1104 screens */
@media (min-width: 884px) and (max-width: 1104px) {
  .services-container {
    padding-top: 12vw;
  }

  .services-hero-section h1 {
    font-size: 2.4rem;
  }

  .services-hero-section p {
    font-size: 1.2rem;
  }

  .service-card h3 {
    font-size: 1.6rem;
  }

  .service-card p {
    font-size: 1rem;
  }
}
